'use client';

import React, { useState, useEffect } from 'react';

const Kpis = () => {
  const [kpis, setKpis] = useState<any[]>([]);
  const [expandedRows, setExpandedRows] = useState<Set<number>>(new Set());

  useEffect(() => {
    const fetchKpis = async () => {
      const res = await fetch('http://localhost:3001/api/kpis');
      const data = await res.json();
      setKpis(data);
    };

    fetchKpis();
  }, []);

  const toggleRow = (index: number) => {
    const newSet = new Set(expandedRows);
    newSet.has(index) ? newSet.delete(index) : newSet.add(index);
    setExpandedRows(newSet);
  };

  const truncateMiddle = (text: string, maxLength = 25) => {
    if (text.length <= maxLength) return text;
    const half = Math.floor(maxLength / 2);
    return `${text.slice(0, half)}...${text.slice(-half)}`;
  };

  const formatDateTime = (dateStr: string) => {
    const date = new Date(dateStr);
    return date.toLocaleString('fr-FR', {
      weekday: 'short',
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <div style={styles.container}>
      <h2 style={styles.title}>Statistiques des Prestations</h2>

      <table style={styles.table}>
        <thead style={styles.tableHeader}>
          <tr>
            <th style={styles.tableCell}>Agence</th>
            <th style={styles.tableCell}>Prestation</th>
            <th style={styles.tableCell}>Date</th>
            <th style={styles.tableCell}>Nombre</th>
          </tr>
        </thead>
        <tbody>
          {kpis.map((kpi, index) => (
            <tr key={index}>
              <td style={styles.tableCell}>{kpi.agence}</td>
              <td style={styles.tableCell}>
                {expandedRows.has(index)
                  ? kpi.prestation
                  : truncateMiddle(kpi.prestation)}
                {kpi.prestation.length > 25 && (
                  <span
                    onClick={() => toggleRow(index)}
                    style={styles.toggleLink}
                  >
                    {expandedRows.has(index) ? ' Voir moins' : ' Voir plus'}
                  </span>
                )}
              </td>
              <td style={styles.tableCell}>{formatDateTime(kpi.date)}</td>
              <td style={styles.tableCell}>{kpi.count}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

const styles: { [key: string]: React.CSSProperties } = {
  container: {
    margin: '40px auto',
    padding: '30px',
    maxWidth: '900px',
    backgroundColor: '#f9f9f9',
    borderRadius: '10px',
    boxShadow: '0 3px 15px rgba(0,0,0,0.1)',
    fontFamily: 'Segoe UI, sans-serif',
    color: '#344767',
  },
  title: {
    textAlign: 'center',
    color: '#007bff',
    marginBottom: '20px',
  },
  table: {
    width: '100%',
    borderCollapse: 'collapse',
  },
  tableHeader: {
    backgroundColor: '#007bff',
    color: 'white',
  },
  tableCell: {
    padding: '10px',
    border: '1px solid #ccc',
    maxWidth: '250px',
    wordBreak: 'break-word',
  },
  toggleLink: {
    color: '#007bff',
    marginLeft: 8,
    cursor: 'pointer',
    fontSize: 12,
    fontStyle: 'italic',
  },
};

export default Kpis;
